import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'BriseSwap',
  description:
    'The most popular AMM on Bitgert by user count! Earn BSWAP through yield farming, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by BriseSwap), NFTs, and more, on a platform you can trust.',
  image: ' ',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('BriseSwap')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('BriseSwap')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('BriseSwap')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('BriseSwap')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('BriseSwap')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('BriseSwap')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('BriseSwap')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('BriseSwap')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('BriseSwap')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('BriseSwap')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('BriseSwap')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('BriseSwap')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('BriseSwap')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('BriseSwap')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('BriseSwap')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('BriseSwap')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('BriseSwap')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('BriseSwap')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('BriseSwap')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('BriseSwap')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('BriseSwap')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('BriseSwap Info & Analytics')}`,
        description: 'View statistics for BriseSwap exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('BriseSwap Info & Analytics')}`,
        description: 'View statistics for BriseSwap exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Pools')} | ${t('BriseSwap Info & Analytics')}`,
        description: 'View statistics for BriseSwap exchanges.',
      }
    default:
      return null
  }
}
